label[for=nav-trigger] {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  width: 60px;
  height: 60px;
  line-height: 60px;
  display: block;
  margin: 0;
  padding: 0;
  z-index: 99;
  transition: transform 0.35s ease-in-out;
  border-left: 1px solid rgba(255, 255, 255, 0.2);
}
label[for=nav-trigger] .label-bg {
  width: 60px;
  height: 60px;
  border: 1px solid rgba(0, 0, 0, 0);
  position: absolute;
  display: block;
  transition: background 0.25s ease-in-out;
}
label[for=nav-trigger] .label-bg:hover {
  background: rgba(0, 0, 0, 0.15);
}
label[for=nav-trigger] .bar {
  display: block;
  position: absolute;
  transition: transform 0.15s ease-in-out, top 0.15s ease-in-out, opacity 0.25s ease-in-out, background 0.25s ease-in-out;
  width: 25px;
  height: 2px;
  left: 17.5px;
  background: #fff;
  transform-origin: 50% 50%;
}
label[for=nav-trigger] .bar1 {
  top: 20px;
}
label[for=nav-trigger] .bar2 {
  top: 28px;
}
label[for=nav-trigger] .bar3 {
  top: 36px;
}

.nav-trigger {
  position: fixed;
  clip: rect(0, 0, 0, 0);
  height: 0;
  width: 0;
}

.main-header {
  position: fixed;
  top: 60px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease-in-out;
  background: rgba(0, 0, 0, 0.9);
  width: 100%;
  z-index: 88;
  font-size: 0.9rem;
}

.nav-trigger:checked ~ .mbl-menu label[for=nav-trigger] .label-bg {
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
}
.nav-trigger:checked ~ .mbl-menu label[for=nav-trigger] .label-bg:hover {
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
}
.nav-trigger:checked ~ .mbl-menu label[for=nav-trigger] .bar {
  background: #fff;
}
.nav-trigger:checked ~ .mbl-menu label[for=nav-trigger] .bar2 {
  opacity: 0;
}
.nav-trigger:checked ~ .mbl-menu label[for=nav-trigger] .bar1 {
  transform: rotate(45deg);
  top: 28px;
}
.nav-trigger:checked ~ .mbl-menu label[for=nav-trigger] .bar3 {
  transform: rotate(-45deg);
  top: 28px;
}

.nav-trigger:checked ~ .main-header {
  max-height: 1800px;
}
.nav-trigger:checked ~ .main-header .nav-wrapper {
  max-height: 800px;
}

