.page-title .sort-filter-menu .nav-link {
  padding: 0.3rem 0.6rem;
}
.page-title .sort-filter-menu ul li:first-child a {
  padding-left: 0;
}
.page-title .sort-filter-menu ul li.active {
  border: 1px solid #e1e1e1;
}
.page-title label {
  font-size: 0.8rem;
