.comments-header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}
.comments-header .medium-title {
  margin: 0;
  align-self: middle;
  margin-top: 20px;
}
.comments-header .login-register {
  margin-top: 20px;
}
.comments-header span {
  color: #999;
  font-size: 0.8rem;
  padding: 0 10px;
}

.article-comment {
  padding-bottom: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #e1e1e1;
  display: flex;
  flex-direction: row;
}
.article-comment .avitar {
  height: 60px;
  width: 60px;
  min-width: 60px;
  border-radius: 50%;
  overflow: hidden;
  background: url(../img/default-avitar.jpg);
  background-size: contain;
}
.article-comment .avitar img {
  max-width: 100%;
  max-height: 100%;
}
.article-comment .avitar a {
  height: 100%;
  width: 100%;
}
.article-comment .comment-body {
  flex-shrink: 25;
  padding-left: 25px;
}
.article-comment .login-register {
  text-align: right;
  font-size: 0.8rem;
}
.article-comment .login-register span {
  padding: 0 10px;
}

.comment-title-wrap {
  margin-bottom: 10px;
}

