}

@media screen and (min-width: 600px) {
  .article-comment .avitar {
    height: 90px;
    width: 90px;
    min-width: 90px;
  }
}
.article-suggestion-nav {
  text-align: center;
  margin: 40px auto 20px auto;
  width: 80%;
}
.article-suggestion-nav button.btn {
  display: block;
  width: 100%;
  margin-bottom: 10px;
}

@media screen and (min-width: 500px) {
  .article-suggestion-nav {
    width: 100%;
  }
  .article-suggestion-nav button.btn {
    display: inline-block;
    width: auto;
    width: initial;
    margin-bottom: 0;
  }
}
.article-suggestion-wrapper {
  display: flex;
  flex-direction: column;
}
.article-suggestion-wrapper .article-suggestion {
  color: #343434;
  text-decoration: none;
  transition: color 0.25s ease-in-out;
  padding: 15px;
  font-size: 1.35rem;
}
.article-suggestion-wrapper .article-suggestion:visited {
  color: #343434;
}
.article-suggestion-wrapper .article-suggestion:visited:hover {
  color: #343434;
}
.article-suggestion-wrapper .article-suggestion:hover, .article-suggestion-wrapper .article-suggestion:focus {
  color: #000;
}
.article-suggestion-wrapper .article-suggestion .img-wrapper {
  margin-bottom: 10px;
  max-height: 400px;
  overflow: hidden;
}
.article-suggestion-wrapper .article-suggestion .img-wrapper img {
  width: 100%;
  max-height: 100%;
}
.article-suggestion-wrapper .article-suggestion p {
  display: -webkit-box;
  white-space: normal;
  word-wrap: normal;
  -webkit-line-clamp: 5;
  line-clamp: 5;
  -webkit-box-orient: vertical;
  box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  max-lines: 5;
}

@media screen and (min-width: 460px) {
  .article-suggestion-wrapper {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .article-suggestion-wrapper .article-suggestion {
    box-sizing: border-box;
    width: 48%;
    float: left;
    flex-grow: 3;
    padding: 15px;
    font-size: 1.35rem;
  }
  .article-suggestion-wrapper .article-suggestion .img-wrapper {
    margin-bottom: 10px;
    max-height: 400px;
    overflow: hidden;
  }
  .article-suggestion-wrapper .article-suggestion .img-wrapper img {
    width: 100%;
    max-height: 100%;
  }
  .article-suggestion-wrapper .article-suggestion p {
    display: -webkit-box;
    white-space: normal;
    word-wrap: normal;
    -webkit-line-clamp: 5;
    line-clamp: 5;
    -webkit-box-orient: vertical;
    box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    max-lines: 5;
  }
}
